import React from "react";
import "./card.styles.css";
export const Card = (props) => (
  <div className="card-container">
    <img alt="crypto" src={props.crypto.image} />
    <h2>{props.crypto.symbol.toUpperCase()} ({props.crypto.name})</h2>
    <h3>Current: ${props.crypto.current_price} USD</h3>
    <h4>24h Highest: ${props.crypto.high_24h} USD</h4>
    <h4>24h Lowest: ${props.crypto.low_24h} USD</h4>
  </div>
); 
