import React, { useState, useEffect } from "react";
import { CardList } from "./components/card-list/card-list.component.jsx";
import { SearchBox } from "./components/search-box/search-box.component.jsx";
import "./App.css";
function App() {

  const [cryptos, setCryptos] = useState([])
  const [searchField, setSearchField] = useState("")
  const [isFetch, setIsFetch] = useState(true)

  useEffect(() => {

    getData()

    let interval = setInterval(() => {
      getData();
      setIsFetch(!isFetch)
    }, 30000);

    return () => {
      clearInterval(interval)
    }

  }, [isFetch])

  function getData() {
    fetch("https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=1&sparkline=false")
    .then((response) => response.json())
    .then((users) => setCryptos(users));
  }


  const filteredcrypto = cryptos.filter((crypto) =>
    crypto ? crypto.name.toLowerCase().includes(searchField.toLowerCase()) : false
  );

  return (
    <div className="App">
    <h1>Cryptocurrency </h1>
      <SearchBox placeholder="Search Your Coin" handlechange={e => setSearchField(e.target.value)} />
      <CardList cryptos={filteredcrypto} />
    </div>
  );
}

export default App;